.Footer {
  position: relative;
  margin-top: 6em;
  width: 100%;
  /* height: var(--footerHeight); */
  background-color: var(--color1);
  padding: 1em 0;
  position: relative;
  max-width: 100%;
}
.Footer .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  flex-wrap: wrap;
  /* max-width: calc(100% - var(--mobileMargin)); */
}
.Footer .SocialMedia {
  margin: 2em 0;
  max-width: 100%;
  margin-left: var(--mobileMargin);
  z-index: 10;
}
.Footer .Copyright {
  margin-top: 5em;
  font-size: 0.7em;
  text-align: right;
  margin-left: 1em;
  margin-right: 1em;
  flex: 1;
  min-width: 16em;
  z-index: 10;
  opacity: 1;
  position: relative;
}
.Footer .Copyright::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: -10;
  background-color: var(--color1);
  opacity: 0.5;
}
.Zombie {
  position: absolute;
  width: var(--zombieHeight);
  height: var(--zombieHeight);
  opacity: 1;
  z-index: 0;
  bottom: 0;
  right: 0;
  background: url("https://i.imgur.com/e9y3FMF.png");
  background-size: cover;
  pointer-events: none;
}
.Icons {
  height: 2em;
  display: flex;
  width: 100%;
  color: #fff;
}

.Icons > * {
  margin: 0.2em;
  height: 100%;
  width: 2em;
  /* background-color: red; */
}
.Icons svg {
  height: 100%;
  width: 100%;
  transition: all 0.3s;
}
.Icons svg:hover {
  opacity: 0.5;
}
