.projectPage.container {
  background-color: var(--color2);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 1em;
}
.projectPage > h2,
.projectPage > h4,
.projectPage > p {
  max-width: 100%;
  padding: 2em 2em;
  text-align: center;
  background-color: var(--color2);
  color: #fff;
}
.projectPage > p {
  max-width: 35em;
}
.projectPage > h2 {
  margin-bottom: -2em;
  font-size: 2em;
}
.projectPage > h4 {
  margin-bottom: -2em;
}
.projectPage > img,
.projectPage > video {
  width: 100%;
  /* margin-bottom: -10px; */
}
.projectPage .link {
  margin-top: 2em;
}
.projectPage .link button {
  padding: 0.5em 1em;
  border-radius: 2px;
  background-color: var(--color2);
  color: var(--color5);
  font-weight: 400;
  border: solid 2px var(--color5);
  transition: all 0.3s;
  font-size: 1em;
}
.projectPage .link button:hover {
  border: solid 2px var(--color1);
  color: var(--color1);
}

.projectPageSelectors {
  margin-top: 2em;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* height: 3em; */
  flex: 1;
}
.buttonProjectPage {
  /* height: 100%; */
  min-height: 3em;
  display: flex;
  min-width: 8em;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9em;
  padding: 0.5em;
  cursor: pointer;
  font-weight: 400;
  background-color: var(--color2);
  color: var(--color5);
  transition: all 0.3s;
}
.buttonProjectPage:hover {
  color: var(--color1);
  /* transform: scale(1.1); */
}
.buttonProjectPage > svg {
  height: 2em;
  /* width: 1em; */
  transition: transform 0.3s;
}

.buttonProjectPage.back:hover svg {
  transform: rotate(-90deg) translateY(-5px) !important;
}
.buttonProjectPage.next:hover > svg {
  transform: rotate(90deg) translateY(-5px) !important;
}
