.NavBar {
  position: sticky;
  top: 0;
  height: 0;
  color: var(--color5);
  text-align: right;
  transition: all 0.5s;
  z-index: 100;
  margin-bottom: 3em;
}
.NavBar .container {
  padding-top: 0.8em;
  z-index: 1000;
}
.NavBarBackground {
  background-color: var(--color2);
  opacity: 0.95;
  mix-blend-mode: multiply;
  position: absolute;
  width: 100%;
  height: var(--navBarHeight);
  z-index: -2000;
}
.Home .NavBar {
  opacity: 1;
  margin-bottom: 0;
}
.Home.Lock .NavBar {
  opacity: 0;
}
.NavBar .content.desktop {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
.NavBar .content * {
  margin-left: 1.5em;
  z-index: 100;
}

.NavBar .content a:hover {
  transform: scale(1.05);
}
.NavBar .selected {
  /* transition: all 0.5s; */
  color: var(--color1);
  font-weight: 800;
  border-bottom: solid 2px var(--color1);
}
.NavBar .languageLabel {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.NavBar .languageLabel .lang {
  padding: 0 0.5em;
  border: none;
  color: gray;
  background: none;
  margin: 0;
  cursor: pointer;
  transition: none;
}
.NavBar .languageLabel .lang:hover {
  transform: scale(1.1);
}
.NavBar .languageLabel div:nth-child(2) {
  border-right: solid gray 2px;
  height: 100%;
  margin: 0;
}

.NavBar .languageLabel .lang.selected {
  border-bottom: none;
  color: var(--color1);
}

/*******************MOBILE*****************/
.content.mobile {
  display: flex;
  justify-content: flex-end;
}
.burger {
  /* background-color: #fff; */

  display: flex;
  flex-direction: column;
  position: relative;
  -webkit-user-select: none;
  user-select: none;
  /* align-self: flex-end; */
  /* justify-content: flex-end; */
}

.burger span {
  position: relative;
  width: 30px;
  height: 4px;
  margin-bottom: 6px;
  background-color: var(--color5);
  transition: all 0.3s;
  margin-right: 1.5em;
  pointer-events: none;
}
.burger span:nth-last-child(2) {
  transform-origin: 100% 100%;
}
.burger span:nth-last-child(4) {
  transform-origin: 100% 0%;
}
.burger input {
  position: absolute;
  margin-left: 0 !important;
  cursor: pointer;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-user-select: none;
  user-select: none;
  outline: none;
}
.mobileMenu {
  margin: 0;
  min-width: 10em;
}
.burger .mobileBurger:checked ~ span {
  background-color: var(--color1);
}
.burger .mobileBurger:checked ~ span:nth-last-child(2) {
  transform: rotate(45deg) translate(-2px, 2px);
}
.burger .mobileBurger:checked ~ span:nth-last-child(3) {
  opacity: 0;
}
.burger .mobileBurger:checked ~ span:nth-last-child(4) {
  transform: rotate(-45deg) translate(-2px, -2px);
}
.burger .mobileBurger:checked ~ span {
  background-color: var(--color1);
}
.mobileMenu {
  display: flex;
  flex-direction: column;
  position: absolute;
  align-items: flex-end;
  top: 2.15em;
  right: 0;
  background-color: var(--color2);
  padding: 1em;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  transform: translateY(-1em);
}
.mobileMenu > * {
  margin: 0.5em;
}
.content.mobile > .burger > .mobileBurger:checked ~ .mobileMenu {
  opacity: 0.95;
  pointer-events: all;
  transform: translate(0);
}
.mobileMenu .languageLabel {
  margin-top: 3em;
}
