:root {
  --color1: #e85d75;
  --color2: #1c3144;
  --color3: #4c5b61;
  --color4: #44344f;
  --color5: #c2f970;
  --degrade: linear-gradient(45deg, var(--color4), var(--color1));
  --navBarHeight: 3em;
  --footerHeight: 8em;
  --zombieHeight: 200px;
  --mediaQ1: 1100px;
  --mobileMargin: 0rem;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  font-family: "Poppins", sans-serif;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0.05);
}
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
button {
  cursor: pointer;
}
.App {
  position: relative;
}
.container {
  width: 1100px;
  margin: auto;
}
.page {
  min-height: calc(100vh - var(--footerHeight) - var(--zombieHeight));
}

.App {
  background-color: var(--color3);
}
.title {
  color: #fff;
  /* background-color: var(--color2); */
  font-size: 3em;
  font-weight: 100;
  padding-top: 0.5em;
  margin: 0.5em var(--mobileMargin);
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.411);
}
/*SCROLL BAR*/
body::-webkit-scrollbar {
  width: 0.8em;
  position: absolute;
  background-color: var(--color2);
}

body::-webkit-scrollbar-track {
  position: absolute;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--color1);
  border-radius: 0.2em;
  outline: none;
}
ul {
  list-style: none;
}
.mobile {
  display: none !important;
}
@media (max-width: 1110px) {
  .container {
    width: 700px;
  }
}
@media (max-width: 710px) {
  :root {
    --mobileMargin: 1rem;
  }
  .container {
    width: 100%;
  }
  .desktop {
    display: none !important;
    /* opacity: 0; */
  }
  .mobile {
    display: flex !important;
  }
}
