.Modal {
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: all 0.3s;
  z-index: 1000;
  scroll-behavior: none;
}
.Modal.active {
  opacity: 1;
  pointer-events: all;
}
.MondalContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  color: aliceblue;
}
.close {
  padding: 1em;
  background: none;
  border: none;
  color: var(--color1);
  width: 4em;
  height: 4em;
  position: absolute;
  top: 1em;
  right: 1em;
  cursor: pointer;
}
.close:hover {
  color: var(--color3);
  transform: rotate(90deg);
  transition: all 0.3s;
}
