.Landing {
  top: 0;
  position: relative;
  background-color: var(--color4);
  background: linear-gradient(45deg, var(--color2), var(--color4));
  background-image: url("https://i.imgur.com/gtj3BQZ.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  transition: all 0.5s;
  font-size: 1.5em;
  margin-bottom: 2em;
  pointer-events: none;
  overflow: hidden;
}
.Landing.On {
  height: 40vh;
}
.Landing .container {
  position: relative;
  height: 100%;
}
.Landing .text {
  position: absolute;
  right: 0;
  height: 100%;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: right;
  text-align: right;
  flex-direction: column;
  color: #fff;
  font-weight: 800;
  text-shadow: 2px 2px 2px black;
  z-index: 50;
  margin-right: var(--mobileMargin);
  transition: all 0.3s;
  transform-origin: 100% 50%;
}
.Landing.On .text {
  transform: translate(0) scale(0.8);
}

.LandingImg {
  background: url("https://i.imgur.com/c6BpEfg.png");
  width: 100%;
  height: 100%;
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left center;
  transition: all 0.5s;
  top: 0;
  left: 0;
  z-index: 10;
  transform-origin: 0 50%;
  transition-delay: 0.3s;
  transform: scale(1.5) translateY(10%);
}
.Landing.On .LandingImg {
  transform: scale(2) translateY(5%) rotate(10deg);
}

@media (orientation: portrait) {
  .LandingImg {
    transform: scale(1.2) translateY(15%) translateX(-25%);
  }
  .Landing.On .LandingImg {
    transform: scale(1.5) translateY(10%) rotate(10deg);
  }
  .Landing .text {
    font-size: 0.8em;
    transform: translateY(-20vh);
  }
  .Landing.On .text {
    transform: translate(0) scale(0.7);
  }
}
