.gridBig {
  width: 100%;
  display: flex;
  flex-wrap: wrap-reverse;
}

.grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  grid-auto-rows: 1fr;
}
.grid::before {
  content: "";
  width: 0;
  padding-bottom: 100%;
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}

.grid > *:first-child {
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}
.foto {
  margin: 1px;
  background: var(--degrade);
  background-size: cover;
  background-position: top center;
  cursor: pointer;
  position: relative;
}
.foto::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--degrade);
  mix-blend-mode: overlay;
  opacity: 0;
  top: 0;
  transition: all 0.3s;
}
.foto:hover::after {
  opacity: 0.5;
}
.gridBig .foto {
  flex: 1;
  min-width: 18rem;
  background-color: #444;
}
.gridBig .foto::before {
  content: "";
  display: block;
  padding-top: 100%;
}
.ModalFotoContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.ModalFotoContainer .modalButton {
  position: absolute;
  height: 100%;
  width: 4em;
  color: var(--color1);
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 100;
}
.ModalFotoContainer .modalButton:hover {
  transform: scale(1.2);
}
.ModalFotoContainer .modalButton:nth-child(1) {
  left: 0;
}
.ModalFotoContainer .modalButton:nth-child(3) {
  right: 0;
}
.ModalFoto {
  max-width: 100%;
  max-height: 100vw;
  display: flex;
  flex-direction: column;
  height: 80vh;
  position: relative;
  background-color: #fff;
  transform: scale(0.5);
  transition: all 0.3s;
}
.Modal.active .ModalFoto {
  transform: scale(1);
}
.ModalFoto img {
  max-height: 100%;
  flex: 1;
}
.loading {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: var(--color1);
  padding: 2em 0;
}
.loadingIcon {
  width: 1em;
  height: 1em;
  margin: 1.5em;
  border-radius: 50% 50% 50% 0;
  border: solid var(--color1) 4px;
  animation: rotation 1.2s infinite linear;
}
@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 710px) {
  .foto:hover::after {
    opacity: 0;
  }
}
