.Project {
  height: 25vh;
  background-color: #ddd;
  background: linear-gradient(45deg, var(--color2), var(--color1));
  margin-top: 1em;
  z-index: 10;
  position: relative;
  overflow: hidden;
  color: white;
  text-shadow: 2px 2px 3px black;
}
.Project .name {
  font-size: 2em;
}
.Project > .content {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 0 2em;
}

.Project .imgBack {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: center center;
  transition: all 0.3s;
  z-index: -100;
  opacity: 0.5;
}
.Project:hover .imgBack {
  transform: scale(1.1);
  opacity: 0.8;
}
