.about {
  padding-bottom: 2em;
  color: #fff;
  /* background-color: var(--color2); */
}
.about .bgc {
  background-color: var(--color2);
}
.textContainer {
  display: flex;
  flex-direction: row;
  margin: 0 var(--mobileMargin);
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 3em;
}
.about .text {
  max-width: 700px;
  color: #fff;
}
.about .text:nth-child(2) {
  padding-top: 2em;
}
.about .text:nth-child(4) {
  padding: 2em 0;
}
.fotoPerfil {
  width: 15em;
  height: 15em;
  margin: auto;
  background: url("https://i.imgur.com/x4obVGQ.jpg");
  background-size: contain;
  border-radius: 50%;
}
.about .extras {
  display: flex;
  width: 100%;

  justify-content: space-between;
  align-items: center;
  margin-top: 2em;
  margin-bottom: -4em;
  flex-wrap: wrap;
}
.about .extras > * {
  flex: 1;
}
.about .SocialMedia {
  flex: 2;
  max-width: calc(100vw - 2em);
}
.about .SocialMedia > * {
  margin: 1em var(--mobileMargin);
}
.about .programas {
  margin: 0 var(--mobileMargin);
  margin-top: 2em;
}
